#Activities{
    background-color: red;
    font-size: var(--fontsize);
}



#Activities #Header{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(59, 59, 59);
}

#Activities #Header h1{
    color: var(--fontColor1);
}

#Activities #Header #Header__option{
    position: absolute;
    right: 0;
    width: 20%;
    height: 80%;
    display: grid;
    grid-template-columns: 30% 50%;

    font-size: 1em;
}

#Activities #Header #Header__option button{
    cursor: pointer;
    border-radius: calc(var(--fontsize)/2) !important;
    font-size: 1em;
}

#Activities #Header #Header__option button + button{
    margin-left: 5%;
}

#showBtn{
    background-color: rgb(96, 255, 33);
    color: var(--fontColor1)
}

#updateBtn{
    background-color: rgb(255, 165, 63);
    color: var(--fontColor1)
}